import axios from 'axios'


axios.defaults.baseURL  = 'https://cnodejs.org/';



export const getAll = (tab,page)=>{
    return axios.get(`/api/v1/topics?tab=${tab}&page=${page}`)
}

export const getDetail = (id)=>{
    return axios.get(`/api/v1/topic/${id}`)
}







