<template>
    <div class="container">
        <div class="top">
            <h3>{{datas.title}}</h3>
            <div>
                <span>作者：{{author.loginname}}</span>
                <span>发布时间：{{datas.create_at|date}}</span>
            </div>
            <p class="btn">加入收藏</p>
        </div>
        <div v-html="htmls"></div>
        <div class="list" v-for="(list,index) in replies" :key="list.id">
            <img :src="list.author.avatar_url">
            <div class="right">
                <span class="name">{{list.author.loginname}}</span> <span>{{index+1}}楼&nbsp;&nbsp;{{list.create_at|date}}</span>
                <div v-html="list.content"></div>
            </div>
        </div>
    </div>
</template>

<script>
import { getDetail } from '../api'
import axios from 'axios'
export default {
    data(){
        return {
            id : this.$route.query.id,
            htmls : '',
            datas:[],
            replies:[],
            author:{}
        }
    },
    created(){  
        this.getDetails()
    },
    methods:{
        getDetails(){
            getDetail(this.id).then(res=>{
                this.datas = res.data.data; 
                this.htmls = res.data.data.content;
                this.replies = res.data.data.replies;
                this.author = res.data.data.author;
            })
        },
        
    }
}
</script>

<style scoped lang="less">
.container{
    max-width:1170px;
    margin:0 auto;
    font-size:14px;
}
.top{
    border-bottom:1px solid #ccc;
    text-align: center;
    h3{
        margin:15px 0 5px; 
    }
    div{
        display:flex;
        justify-content:space-around;
        margin-bottom: 5px;
    }
}
.btn{
    padding:5px 10px;
    background:#369219;
    color:#fff;
    margin:5px auto;
}
.list{
    display: flex;
    padding:10px 5px;
    border-bottom:1px dashed #ccc;
    img{
        width:45px;
        height:45px;
        margin-right:10px;
    }
    .right{
        color:#79b6f7;
        .name{
            color:#000;
        }
    }
}
</style>



